.SuperCheckbox_container__KIUVX {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 16px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.SuperCheckbox_container__KIUVX input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.SuperCheckbox_checkmark__2smDY {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */
.SuperCheckbox_container__KIUVX:hover input ~ .SuperCheckbox_checkmark__2smDY {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.SuperCheckbox_container__KIUVX input:checked ~ .SuperCheckbox_checkmark__2smDY {
    background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.SuperCheckbox_checkmark__2smDY:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.SuperCheckbox_container__KIUVX input:checked ~ .SuperCheckbox_checkmark__2smDY:after {
    display: block;
}

/* Style the checkmark/indicator */
.SuperCheckbox_container__KIUVX .SuperCheckbox_checkmark__2smDY:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
}
